<template>
  <v-container
    id="dashboard-view"
    fluid
    tag="section"
  >
    <v-row>     
      <v-col
        cols="12"
      >
        <material-card
          color="green"
          full-header
        >
          <template #heading>
            <v-tabs
              v-model="tabs"
              background-color="transparent"
              slider-color="white"
              class="pa-3"
              show-arrows
            >
              <v-tab class="mr-3 font-weight-bold text-button" href="#SubjectList" @click="updateRoute('#SubjectList')">
                {{ $vuetify.lang.t('$vuetify.chi') }}
              </v-tab>
              <v-tab class="mr-3 font-weight-bold text-button" href="#SubjectList2" @click="updateRoute('#SubjectList2')">
                {{ $vuetify.lang.t('$vuetify.eng') }}
              </v-tab>
              <v-tab class="mr-3 font-weight-bold text-button" href="#Maths" @click="updateRoute('#Maths')">
                {{ $vuetify.lang.t('$vuetify.maths') }}
              </v-tab>                             
            </v-tabs>
          </template>
          <v-tabs-items
            v-model="tabs"
            background-color="transparent"
          >
            <v-tab-item id="SubjectList" key="SubjectList">                      
              <v-card-text>
                  <v-lazy :options="{ threshold: 0.5 }" min-height="200" transition="fade-transition">  
                    <SubjectList />
                  </v-lazy>
              </v-card-text>             
            </v-tab-item>
            <v-tab-item id="SubjectList2" key="SubjectList2">         
              <v-card-text>
                <v-lazy :options="{ threshold: 0.5 }" min-height="200" transition="fade-transition"> 
                <SubjectList2 />
                </v-lazy>
              </v-card-text>              
            </v-tab-item>
            <v-tab-item id="Maths" key="Maths">                        
              <v-card-text>
                <v-lazy :options="{ threshold: 0.5 }" min-height="200" transition="fade-transition"> 
                <SchoolList />
                </v-lazy>
              </v-card-text>             
            </v-tab-item>                         
          </v-tabs-items>                                      
        </material-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
  // Components
  import AccountList from '@/components/AccountList'
  import AccountRightList from '@/components/AccountRightList'
  import SchoolList from '@/components/SchoolList'
  import SubjectList from '@/components/SubjectList'
  import SubjectList2 from '@/components/SubjectList2'

  export default {
    name: 'Subject',
    components: { SubjectList, AccountList, AccountRightList, SchoolList, SubjectList2 },
    data: () => ({
      dialog: false,
      dialogDelete: false,
      tabs: '',
    }), 
    methods: {
      updateRoute (route) {
        this.$router.push({ path: route })
      }      
    },
    mounted() {
      this.tabs = this.$route.hash == '' ? 'AccountList' : this.$route.hash.replace('#', '');
    },     
  }
</script>